<template>
  <div class="point-common-com" :style="comStyle" :class="[`status-${status}`, actived ? 'actived' : '']" @click="nodeClick">
    <div class="bg-box"></div>
    <i v-if="type === 'train'" class="iconfont icon-daojiaozhengfangxing"></i>
    <i v-else-if="type === 'daily'" class="iconfont icon-circle"></i>
    <i v-else-if="type === 'synthesis'" class="iconfont icon-sanjiaoxing1"></i>
    <i v-else-if="type === 'assess'" class="iconfont icon-wubianxing1"></i>
    <div class="point-label" :class="[ position ]">
      <div class="label-line"></div>
      <div class="label-box">{{ label }}</div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'PointCommon',
  props: {
    type: {
      type: String,
      default: 'train',
      validator: function (value) {
        return ['train', 'daily', 'synthesis', 'assess'].includes(value)
      }
    },
    status: {
      type: String,
      default: '1',
      validator: function (value) {
        // 0 根节点 不属于数据状态
        // 1 未提交
        // 2 已提交
        // 3 退回修改
        // 4 待审核
        return ['0', '1', '2', '3', '4'].includes(value)
      }
    },
    label: String,
    top: {
      type: Number,
      default: -1000
    },
    left: {
      type: Number,
      default: -1000
    },
    position: {
      type: String,
      default: 'top',
      validator: function (value) {
        return ['top', 'bottom'].includes(value)
      }
    }
  },
  data() {
    return {
      actived: false
    }
  },

  created() {
  },

  computed: {
    comStyle() {
      return {
        top: this.top + 'px',
        left: this.left + 'px',
      }
    }
  },

  methods: {
    nodeClick() {
      if (this.status === '0') return
      this.actived = !this.actived
    }
  }
}
</script>

<style lang="scss" scoped>
$-stutus-0: #ffffff;
$-stutus-1: #2395F6;
$-stutus-2: #26CAD3;
$-stutus-3: #FF6363;
$-stutus-4: #F79E00;
.point-common-com {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  position: absolute;
  cursor: pointer;
  transform: translate(-10px, -10px);
  .bg-box {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    transition: all .3s;
    position: absolute;
    z-index: 2;
    transition: all .3s;
  }
  .iconfont {
    width: 16px;
    height: 16px;
    font-size: 16px;
    transition: all .3s;
    display: block;
    position: relative;
    z-index: 3;
  }
  .point-label {
    width: 61px;
    height: 16px;
    text-align: center;
    background-color: rgba(152, 161, 168, .4);
    border-radius: 2px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    transition: background-color .3s;
    .label-line {
      width: 0;
      height: 0;
      border-left: 1px dashed rgba(152, 161, 168, .4);
      position: absolute;
      left: 30px;
      transition: all .3s;
    }
    .label-box {
      font-size: 12px;
      color: #fff;
      transform: scale(0.833);
      
    }
    &.top {
      bottom: 100px;
      .label-line {
        height: 84px;
        top: 16px;
      }
    }
    &.bottom {
      top: 100px;
      .label-line {
        height: 84px;
        bottom: 16px;
      }
    }
  }

  &.status-0 {
    color: $-stutus-0;
  }
  &.status-1 {
    color: $-stutus-1;
  }
  &.status-2 {
    color: $-stutus-2;
  }
  &.status-3 {
    color: $-stutus-3;
  }
  &.status-4 {
    color: $-stutus-4;
  }
  &:hover, &.actived {
    &.status-0 {
      .bg-box {
        background-color: $-stutus-0;
      }
    }
    &.status-1 {
      .bg-box {
        background-color: $-stutus-1;
      }
    }
    &.status-2 {
      .bg-box {
        background-color: $-stutus-2;
      }
    }
    &.status-3 {
      .bg-box {
        background-color: $-stutus-3;
      }
    }
    &.status-4 {
      .bg-box {
        background-color: $-stutus-4;
      }
    }
    .iconfont {
      color: #fff;
      transform: scale(.5);
    }
    .point-label {
      background-color: rgba(39, 40, 42, 0.4);
      .label-line {
        border-left: 1px dashed rgba(39, 40, 42, 0.4);
      }
    }
  }
  &.actived {
    .bg-box {
      box-shadow: 0px 0px 10px 8px rgba(0, 0, 0, .2);
    }
    .point-label {
      background-color: rgba(39, 40, 42, 0.6);
      .label-line {
        border-left: 1px dashed rgba(39, 40, 42, 0.6);
      }
    }
  }
  
}
</style>
