<template>
  <div class="svg-path-com" v-loading="pathDataloading" element-loading-background="rgba(0, 0, 0, 0)">
    <svg class="svg-root" xmlns="http://www.w3.org/2000/svg" version="1.1">
    <defs>
      <linearGradient id="grad1" x1="0%" y1="0%" x2="100%" y2="0%">
        <stop offset="0%" style="stop-color: #AE56DF; stop-opacity:1" />
        <stop offset="100%" style="stop-color: #3B7BEA; stop-opacity:1" />
      </linearGradient>
      <!-- <path id="pathColor" :d="pathData" stroke="url(#grad1)" transform="translate(-600, 400)"  stroke-width="26" fill="none" stroke-linecap="round" stroke-linejoin="round" /> -->
      <path id="pathColor" :d="pathData"  transform="translate(0, 0)" stroke="url(#grad1)" stroke-width="26" fill="none" stroke-linecap="round" stroke-linejoin="round" />
      <!-- <mask id="mask1">
        <path d="M -13.333333333333332 693.3333333333333 L 164 800 C 168 802 174 802 177 798 L 316 666.6666666666666  C 317 665.6666666666666 318 665.6666666666666 319 666.6666666666666 L 330 674" stroke="#fff"  stroke-width="26" fill="none" stroke-linecap="round" stroke-linejoin="round" />
      </mask>
      <mask id="mask2">
        <path d="M 236 742 L 316 666.6666666666666 C 317 665.6666666666666 318 665.6666666666666 319 666.6666666666666 L 330 674" stroke="#fff"  stroke-width="26" fill="none" stroke-linecap="round" stroke-linejoin="round" />
      </mask> -->
    </defs>

      <!-- 灰色路径 -->
      <!-- <path :d="pathData" stroke="#D8D8E1" transform="translate(-600, 400)"  stroke-width="26" fill="none" stroke-linecap="round" stroke-linejoin="round" /> -->
      <path :d="pathData" stroke="#D8D8E1" transform="translate(0, 0)"  stroke-width="26" fill="none" stroke-linecap="round" stroke-linejoin="round" />
      <!-- <use
        xlink:href="#pathColor"
        mask="url(#mask1)"
        stroke-dasharray="1, 2"
      ></use> -->
      
      <!-- 渐变路径 -->
      <!-- <path :d="pathData" stroke="url(#grad1)"  mask="url(#mask1)"  stroke-width="26" fill="none" stroke-linecap="round" stroke-linejoin="round"  /> -->
      <use
        xlink:href="#pathColor"
        :stroke-dasharray="strokeDasharray"
      >
      </use>
    </svg>
    <template v-for="(item, index) in pointList">
      <PointCommon :key="index" v-bind="item" :left="multiplyByRation(item.left)" :top="multiplyByRation(item.top)" />
    </template>
    <template v-if="total > 0">
      <el-pagination
        :disabled="paginationDisabled"
        :style="{top: multiplyByRation(450) + 'px'}"
        class="path-pagination-box"
        layout="prev, pager, next"
        :current-page="currenPage"
        @current-change="currentChangeHandler"
        :total="1000">
      </el-pagination>
      <div class="example-box" :style="{top: multiplyByRation(520) + 'px'}">
        <div class="eg-item">
          <i class="iconfont icon-daojiaozhengfangxing"></i>
          <span class="eg-name">教师培训课程</span>
        </div>
        <div class="eg-item">
          <i class="iconfont icon-circle"></i>
          <span class="eg-name">日常能力成长任务</span>
        </div>
        <div class="eg-item">
          <i class="iconfont icon-sanjiaoxing1"></i>
          <span class="eg-name">综合能力考核任务</span>
        </div>
        <div class="eg-item">
          <i class="iconfont icon-wubianxing1"></i>
          <span class="eg-name">能力评估</span>
        </div>
      </div>
      <div v-if="currenPage > 1" class="pre-btn" :class="{disbaled: paginationDisabled}" @click="animatePre">上一页 <i class="el-icon-sort-up"></i></div>
      <div v-if="currenPage < allPages" class="next-btn" :class="{disbaled: paginationDisabled}" @click="animateNext">下一页 <i class="el-icon-sort-up"></i></div>
    </template>
  </div>
</template>

<script>
function debounce (callback, delay) {
  let lastTime
  return function (...args) {
    clearTimeout(lastTime)
    const that = this
    lastTime = setTimeout(() => {
      callback.apply(that, args)
    }, delay)
  }
}
const TWEEN = require('@tweenjs/tween.js')
import PointCommon from './PointCommon'
import mixin1 from './mixin_1'

export default {
  name: 'SvgPath',
  mixins: [mixin1],
  data() {
    return {
      // 水平方向 路径长度
      staticX: 1542,
      // 垂直方向 路径长度
      staticY: 550,
      // 基础倍率
      ratio: 1,
      pathData: '',
      currenPage: 1,
      allPages: 100,
      total: 0,
      strokeDasharray: [0],
      paginationDisabled: false,
      animationDuration: 500,
      pathDataloading: true
    }
  },

  components: {
    PointCommon
  },

  mounted() {
    window.addEventListener('resize', this.viewSizeInit)
    this.viewSizeInit()
  },

  beforeDestroy() {
    window.removeEventListener('resize', this.viewSizeInit)
  },

  methods: {
    /**
     * @description: 基准倍率转换
     * @param {*} val
     * @return {*}
     */
    multiplyByRation(val) {
      const res = val * this.ratio
      return res
    },
    /**
     * @description: 初始化
     * @param {*} function
     * @return {*}
     */
    viewSizeInit: debounce(function() {
      if (this.initingFalg) return
      this.initingFalg = true
      const width = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth
      if (width > 1440) {
        const ratio = width / 1440
        this.ratio = ratio
        this.$nextTick(() => {
          this.$el.style.width = this.multiplyByRation(1440) + 'px'
          // this.$el.style.height = this.multiplyByRation(844) + 'px'
          this.$el.style.height = this.multiplyByRation(640) + 'px'
        })
      } else {
        this.$el.style.width = '1440px'
        // this.$el.style.height = '844px'
        this.$el.style.height = '640px'
        this.ratio = 1
      }
      this.pathRender()
      this.pointRender()
      this.initingFalg = false
      this.pathDataloading = false
      this.total = 2500
    }, 500),

    /**
     * @description: 路径生成
     * @return {*}
     */
    pathRender() {
      const transX = (this.currenPage - 1) * this.staticX * -1
      const transY = (this.currenPage - 1) * this.staticY
      const pathList = []
      for (let i=0;i<=100;i++) {
        const cx = this.staticX * i + transX
        const cy = -this.staticY * i + transY
        const nextPathD = this.staticPathDList.map(item => {
          return [item[0] + cx, item[1] + cy]
        })
        pathList.push(...nextPathD)
      }
      
      this.pathDList = pathList
      this.pathData = this.getPathData(this.pathDList)
    },

    /**
     * @description: 点生成
     * @return {*}
     */
    pointRender() {
      const transX = (this.currenPage - 1) * this.staticX * -1
      const transY = (this.currenPage - 1) * this.staticY
      const pointList = []
      for (let i=0;i<=3;i++) {
        const cx = this.staticX * i
        const cy = -this.staticY * i
        this.pointBasicData.forEach(item => {
          pointList.push(
            {
              ...item,
              left: item.left + cx + transX,
              top: item.top + cy + transY,
              len: (item.len + this.allPathLen * i) * this.ratio
            }
          )
        })
      }
      this.pointList = [{
          ...this.startPoint,
          left: this.startPoint.left + transX,
          top: this.startPoint.top + transY,
        }, ...pointList]
      this.adjoinRender(pointList)
    },
    
    /**
     * @description: 相邻完成点 
     * @return {*}
     */
    adjoinRender(pointList) {
      const adjoinPointList = []
      let arr = []
      pointList.forEach((item, index) => {
        if (item.status === '2') {
          arr.push({
            item,
            index
          })
        } else {
          if (arr.length > 1) {
            adjoinPointList.push([...arr])
          }
          arr = []
        }
      })
      console.log(adjoinPointList)
      this.pointConnectAnimation(adjoinPointList)
    },

    /**
     * @description: 点连接动画
     * @return {*}
     */
    pointConnectAnimation(adjoinPointList) {
      
      const strokeList = []
      if (adjoinPointList.length) {
        if (adjoinPointList[0][0].index > 0) {
          strokeList.push(0)
          adjoinPointList.forEach(item => {
            const start = item[0].item.len
            const end = item[item.length - 1].item.len
            strokeList.push(end - start)
          })
        } else {
          adjoinPointList.forEach((item, index) => {
            if (index === 0) {
              strokeList.push(item[item.length - 1].item.len)
            } else {
              const lastItem = adjoinPointList[index - 1]
              const sub = item[0].item.len - lastItem[lastItem.length - 1].item.len
              strokeList.push(sub)
              const start = item[0].item.len
              const end = item[item.length - 1].item.len
              strokeList.push(end - start)
            }
          })
        }
      }
      this.strokeDasharray = [...strokeList, this.allPages * this.allPathLen]
      // console.log(this.strokeDasharray)
      return
      /* eslint-disable */
      console.log(strokeList)
      
      const vm = this
      const tween = new TWEEN.Tween({ stroke: new Array(strokeList.length).fill(0) })

      function animate () {
        if (tween.update()) {
          requestAnimationFrame(animate)
        }
      }

      tween.to({stroke: strokeList}, 2000)
      .repeat(Infinity)
      // .easing(TWEEN.Easing.Cubic.Out)
      // .easing(TWEEN.Easing.Back.Out)
      .easing(TWEEN.Easing.Exponential.Out)
      // .easing(TWEEN.Easing.Bounce.Out)
      .onUpdate(function (obj) {
        const { stroke } = obj
        vm.strokeDasharray = [...stroke, vm.strokeDasharray[1]]
        console.log(stroke)
      })
      .start()

      animate()
    },
    
    /**
     * @description: 切页动画
     * @param {*} cx
     * @param {*} cy
     * @return {*}
     */
    animationStart(cx, cy) {
      
      const vm = this
      const tween = new TWEEN.Tween({cx: 0, cy: 0})
      function animate () {
        if (tween.update()) {
          requestAnimationFrame(animate)
        }
      }
      const pointList = this.pointList.map(item => {
        return {
          ...item
        }
      })
      console.log(new TWEEN.Tween())

      tween.to({cx, cy}, this.animationDuration)
      .easing(TWEEN.Easing.Cubic.InOut)
      .onUpdate(function (obj) {
        const { cx, cy } = obj
        const pathDList = vm.pathDList.map(item => {
          return [item[0] + cx, item[1] + cy]
        })
        vm.pathData = vm.getPathData(pathDList)
        for (let i=0,len=pointList.length;i<len;i++) {
          vm.pointList[i].left = pointList[i].left + cx
          vm.pointList[i].top = pointList[i].top + cy
        }
      })
      .onComplete(function(obj) {
        console.log('动画结束：', obj)
        const { cx, cy } = obj
        vm.pathDList.forEach(item => {
          item[0] = item[0] + cx
          item[1] = item[1] + cy
        })
        vm.paginationDisabled = false
      })
      .start()

      animate()
    },
    animatePre() {
      if (this.paginationDisabled || this.currenPage <= 1) return
      this.currentChangeHandler(this.currenPage - 1)
    },
    animateNext() {
      if (this.paginationDisabled || this.currenPage >= 100) return
      this.currentChangeHandler(this.currenPage + 1)
    },
    sleep() {
      return new Promise(resolve => {
        const sleepTime = Math.random() * 1000
        setTimeout(() => {
          resolve()
        }, sleepTime)
      })
    },
    async currentChangeHandler(val, disableAnimate) {
      this.paginationDisabled = true
      this.pathDataloading = true
      await this.sleep()
      this.pathDataloading = false
      this.$emit('pageChange', val)
      console.log('翻页--', '当前页：', this.currenPage, '目标页：',val, )
      const diffValue = val - this.currenPage
      if (!disableAnimate) {
        if (Math.abs(diffValue) > 40) {
          this.animationDuration = 300
        } else {
          this.animationDuration = 500
        }
      } else {
        this.animationDuration = 0
      }
      const cx = -this.staticX * diffValue 
      const cy = this.staticY * diffValue
      this.animationStart(cx, cy)
      this.currenPage = val
    }
  }
}
</script>

