  methods: {
    getPathData(pointArr) {
      let resD = ''
      pointArr.forEach((item, index) => {
        if (index === 0) {
          resD += `M ${ this.multiplyByRation(item[0])} ${ this.multiplyByRation(item[1])} `
        } else if (index === pointArr.length - 1) {
          resD += `L ${ this.multiplyByRation(item[0]) } ${ this.multiplyByRation(item[1]) } `
        } else {
          if (index % 2 === 0) {
            // L 237 500 C 238 499 239 499 240 500 
            // resD += `L ${ item[0] * ratio } ${ item[1] * ratio } C ${ (item[0]+1) * ratio } ${ (item[1]-1) * ratio } ${ (item[0]+2) * ratio } ${ (item[1]-1) * ratio } ${ (item[0]+3) * ratio } ${ item[1] * ratio } `
            // resD += `L ${ this.multiplyByRation(item[0]) } ${ this.multiplyByRation(item[1])  } C ${ this.multiplyByRation(item[0])  + 1 } ${ this.multiplyByRation(item[1])  - 1 } ${ this.multiplyByRation(item[0])  + 2 } ${ this.multiplyByRation(item[1])  - 1 } ${ this.multiplyByRation(item[0])  + 3 } ${ this.multiplyByRation(item[1])  } `
            resD += `L ${ this.multiplyByRation(item[0]) } ${ this.multiplyByRation(item[1])  } C ${ this.multiplyByRation(item[0] + 1) } ${ this.multiplyByRation(item[1] - 1) } ${ this.multiplyByRation(item[0] + 2) } ${ this.multiplyByRation(item[1] - 1) } ${ this.multiplyByRation(item[0] + 3)} ${ this.multiplyByRation(item[1])  } `
          } else {
            // L 123 600 C 127 602 133 602 136 598 
            // resD += `L ${ item[0] * ratio } ${ item[1] * ratio } C ${ (item[0]+4) * ratio } ${ (item[1]+2) * ratio } ${ (item[0]+10) * ratio } ${ (item[1]+2) * ratio } ${ (item[0]+13) * ratio } ${ (item[1]-2) * ratio } `
            // resD += `L ${ this.multiplyByRation(item[0])  } ${ this.multiplyByRation(item[1])  } C ${ this.multiplyByRation(item[0]) + 4 } ${ this.multiplyByRation(item[1]) + 2 } ${ this.multiplyByRation(item[0]) + 10 } ${ this.multiplyByRation(item[1]) + 2 } ${ this.multiplyByRation(item[0]) + 13 } ${ this.multiplyByRation(item[1]) - 2 } `
            resD += `L ${ this.multiplyByRation(item[0])  } ${ this.multiplyByRation(item[1])  } C ${ this.multiplyByRation(item[0] + 4) } ${ this.multiplyByRation(item[1] + 2) } ${ this.multiplyByRation(item[0] + 10) } ${ this.multiplyByRation(item[1] + 2) } ${ this.multiplyByRation(item[0] + 13) } ${ this.multiplyByRation(item[1] - 2) } `
          }
        }
      })
      return resD
    }
  }
